.template-news {
  >.row-2 {
    >.wrapper {
      .news {
        margin: 20px 0 0 0;
        width: 100%;
        @media screen and (min-width: $tabwidth) {
          width: (1/3)*97%;
          float: left;
          margin: 1.5% 1.5% 0 0;
        }
        &:first-child {
          margin: 15px 0 0 0;
          @media screen and (min-width: $tabwidth) {
            margin: 1.5% 1.5% 0 0;
          }
        }
        &.count-2 {
          @media screen and (min-width: $tabwidth) {
            margin-right: 0;
          }
        }
        a {
          display: block;
          text-decoration: none;
          color: inherit;
